.card {
  backdrop-filter: blur(5px);
  width: 350px;
  height: 432px;
  padding: 40px 25px 50px 35px;
  box-shadow: 0px 0px 250px 0px rgba(255, 255, 255, 0.03) inset;
  backdrop-filter: blur(20px);
  position: relative;
  transition: box-shadow 0.3s, transform 0.3s;
  transform-style: preserve-3d;
  border-radius: 40px;
  border: 0.3px solid #a5a5a5;
  overflow: hidden;
  cursor: default;
}

.glare-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.card__description {
  margin-top: 30px;
  text-align: left;
}

.card__term {
  width: 100%;
  margin-top: 30px;
  color: var(--white-80);
  text-align: left;
}

.card__price {
  width: 100%;
  margin-top: 10px;
  color: var(--white-80);
  text-align: left;
  cursor: default;
}

.card__button {
    margin-top: 50px;
    width: 220px;
    height: 50px;
    border-radius: 10px;
    background: var(--brand-color);
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--white);
    font-size: 18px;
}

  .card__glare {
    position: absolute;
    width: 80px;
    height: 80px;
    background-color: var(--brand-color);
    filter: blur(60px);
    border-radius: 50%;
    pointer-events: none;
    transform: translate(-50%, -50%) scale(0);
    transition: transform 0.3s ease;
    z-index: -1;
  }
  
  .card:hover .card__glare {
    transform: translate(-50%, -50%) scale(1);
  }