.calculator__form {
  margin-top: 9px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.calculator__input {
  border: none;
  border-bottom: 2px solid rgba(255, 255, 255, 0.2);
  background-color: inherit;
  outline: none;
  color: white;
  vertical-align: bottom;
  line-height: normal;
  padding: 35.5px 0px 3px 0px;
  text-align: left;
}

.calculator__input-error {
  border: none;
  border-bottom: 2px solid #f00;
  color: #f00;
  padding: 35.5px 0px 3px 0px;
  text-align: left;
  vertical-align: bottom;
  line-height: normal;
  background-color: inherit;
  outline: none;
}

.calculator__input-error::placeholder {
  color: #f00;
}

.calculator__input::placeholder {
  color: rgba(255, 255, 255, 0.3);
}
