.accordion {
    margin-top: 60px;
    width: 100%;
}

.accordion-item {
    position: relative;
    border-bottom: 1px solid rgba(255, 255, 255, 0.20);
    border-radius: 2px;
    padding: 0px 0px 30px 0px;
    list-style-type: none;
    cursor: pointer;
}

.accordion-item:first-child {
    border-top: 1px solid rgba(255, 255, 255, 0.20);
    border-radius: 2px;
}

.accordion-header {
    background-color: inherit;
    color: white;
    border: none;
    padding-top: 30px;
    width: 100%;
    align-items: center;
    text-align: left;
    font-size: 24px;
}

.accordion-body {
    margin-top: 20px;
    margin-left: 5px;
    text-align: left;
    color: var(--white-80);
    width: 1034px;
}

.accordion-collapse {
    height: 0px;
    overflow: hidden;
    transition: height .4s;
}

.accordion-collapse.open {
   height: auto;
}

.plus__icon {
    width: 29px;
    top: 29px;
    position: absolute;
    right: 0;
    top: 30px;
    transition: .4s ease-in-out;
}

.plus__icon.active {
    width: 29px;
    top: 29px;
    position: absolute;
    right: 0;
    top: 30px;
    transform: rotate(45deg);
}