.header__fixed {
  width: 100%;
  height: 100%;
  position: fixed;
  background: rgba(0, 0, 0, 0.15);
  backdrop-filter: blur(5px);
  display: flex;
  justify-content: center;
  height: 90px;
  z-index: 5;
  transform: translate3d(0, 0, 0);
}

.header {
  padding: 20px 0px;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  width: 101px;
  height: 48px;
}

.header__nav {
  width: 573px;
  display: flex;
  justify-content: space-between;
}

.header__buttons {
  width: 248px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.support__button {
  width: 24px;
  height: 24px;
  border: none;
  border-radius: 50%;
  background: linear-gradient(to bottom, #1c6eda 50%, #faff00 50%);
}

.header__button {
  width: 200px;
  height: 50px;
  border: none;
  background-color: var(--brand-color);
  color: #fff;
  font-family: "Inter-SB";
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 0.54px;
  border-radius: 10px;
}
