/* Inter font import */
@font-face {
  font-family: "Inter-R";
  src: url("./assets/fonts/Inter-font/static/Inter-Regular.ttf");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Inter-M";
  src: url("./assets/fonts/Inter-font/static/Inter-Medium.ttf");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Inter-SB";
  src: url("./assets/fonts/Inter-font/static/Inter-SemiBold.ttf");
  font-weight: normal;
  font-style: normal;
}

/* Alice font import */
@font-face {
  font-family: "Alice-R";
  src: url("./assets/fonts/Alice/Alice-Regular.ttf");
  font-weight: normal;
  font-style: normal;
}

/* Montserrat font import */
@font-face {
  font-family: "Montserrat-R";
  src: url("./assets/fonts/Montserrat /static/Montserrat-Regular.ttf");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat-M";
  src: url("./assets/fonts/Montserrat /static/Montserrat-Medium.ttf");
  font-weight: normal;
  font-style: normal;
}

/* Colors */
:root {
  --white: #fff;
  --white-80: rgba(255, 255, 255, 0.80);;
  --brand-color: #1c6eda;
  --body-text: rgba(255, 255, 255, 0.8);
}

/* Reset */
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

/* Body Styles */
body {
  margin: 0;
  padding: 0;
  background-color: black;
  color: white;
  /* font-family: "Montserrat Alternates", sans-serif; */
}

/* Image Styles */
img {
  max-width: 100%;
  display: block;
}

/* Form Element Styles */
input,
button,
select {
  font: inherit;
}

/* Button Styles */
button {
  cursor: pointer;
}

a {
  text-decoration-line: none;
}

/*  Containers styles */

.global__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.container {
  display: flex;
  width: 1200px;
}

/* Font Styles */

.brand-color {
  color: var(--brand-color);
}

.text-base-sb {
  color: white;
  font-family: "Inter-SB";
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 19px;
}

.text-base-r {
  text-align: center;
  font-family: "Inter-R";
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
}

.text-title-r {
  color: var(--white);
  font-family: "Alice-R";
  font-size: 60px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.text-title-2 {
  font-family: "Montserrat-M";
  font-size: 28px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.text-title-3 {
  text-align: center;
  font-family: "Montserrat-M";
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.text-title-5 {
  font-family: "Montserrat-M";
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.text-title-4 {
  font-family: "Montserrat-M";
  font-size: 42px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}


