.main_calculator {
  padding-top: 80px;
  padding-bottom: 80px;
  width: 100%;
  display: flex;
  justify-content: center;
  position: relative;
  background: #000;
}

.main_calculator-wrapper {
  display: flex;
  justify-content: space-between;
  width: 1200px;
}

.main_calculator-texts {
  margin-left: 15px;
  width: 439px;
}

.main_calculator-title {
  margin-top: 55px;
}

.main_calculator-description {
  margin-top: 20px;
  text-align: left;
  color: var(--white-80);
}

.main_calculator-description:nth-child(3) {
  margin-top: 60px;
}

.form__description {
  margin-top: 10px;
  text-align: left;
  color: var(--white-80);
}

.main__elipse-5 {
  top: 100px;
  position: absolute;
  width: 100%;
  height: 200px;
  flex-shrink: 0;
  border-radius: 1600px;
  background: rgba(28, 110, 218, 0.4);
  filter: blur(100px);
  z-index: -1;
  border-radius: 50%;
}

.main__elipse-6 {
  top: 370px;
  position: absolute;
  width: 100%;
  height: 200px;
  flex-shrink: 0;
  border-radius: 1600px;
  background: rgba(28, 110, 218, 0.4);
  filter: blur(100px);
  z-index: -1;
  border-radius: 50%;
}
