.footer {
  width: 100%;
  background: #000;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer__container.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer__top {
  width: 108.33333%;
  padding: 38.5px 0px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 2px;
}

.footer__top-list {
  padding: 0;
  display: flex;
  gap: 75px;
  margin-left: 50px;
}

.footer__top-link {
  color: var(--white-80);
  text-decoration: underline;
}

.footer__top-contacts {
  padding: 0;
  margin-right: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 75px;
}

.footer__contact {
  color: var(--white-80);
}

.footer__middle {
    width: 108.33333%;
    margin-top: 40px;
    padding-bottom: 40px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 2px;
    display: flex;
    justify-content: space-between;
}

.footer__fogo {
  margin-left: 50px;
  font-size: 42px;
}

.footer__bottom {
    width: 108.33333%;
    margin-top: 35px;
    padding-bottom: 35px;
    display: flex;
    justify-content: space-between;
}

.footer__copyright {
    margin-left: 50px;
    color: rgba(255, 255, 255, 0.25);
}

.working__time{
    color: var(--white-80);
    margin-right: 50px;
}