.callback__form-section {
  margin-top: 120px;
  width: 100%;
  border: 0.3px solid rgba(255, 255, 255, 0.2);
  background: rgba(0, 0, 0, 0.8);
  box-shadow: 0px 0px 250px 300px rgba(255, 255, 255, 0.01) inset;
  backdrop-filter: blur(20px);
  border-left: none;
  border-right: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}

.callback__form-wrapper.container {
  position: relative;
  margin-top: 60px;
  margin-bottom: 57px;
  display: flex;
  justify-content: space-between;
}

.callback__form-elipse {
  position: absolute;
  left: 410px;
  top: 10px;
  border-radius: 50%;
  width: 200px;
  height: 200px;
  flex-shrink: 0;
  background: rgba(28, 110, 218, 0.40);
  filter: blur(140px);
}

.callback__form-texts {
  margin-left: 80px;
  width: 397px;
  display: flex;
  flex-direction: column;
  gap: 21px;
}

.callback__form-title {
  width: 329px;
  color: var(--white);
}

.callback__form-description {
  text-align: left;
}

.callback__form-description:nth-child(4) {
  color: var(--white-80);
}

.question__mark {
  margin-left: 4.5px;
  font-size: 42px;
}
