.calculator__container {
  padding: 40px 46px 30px 46px;
  width: 652px;
  height: 510px;
  border-radius: 40px;
  border: 0.3px solid #a5a5a5;
  background: rgba(0, 0, 0, 0.8);
  box-shadow: 0px 0px 250px 300px rgba(255, 255, 255, 0.01) inset;
  backdrop-filter: blur(20px);
  position: relative;
  overflow: hidden;
}

.violet__round {
  position: absolute;
  width: 80px;
  height: 80px;
  background: #d136d0;
  filter: blur(80px);
  z-index: -1;
  top: 150px;
  left: 342px;
}

.calculator__title {
  text-align: left;
  margin-left: 2px;
}

.calculator__questions-wrapper {
  margin-top: 31px;
  height: 210px;
  display: flex;
  row-gap: 30px;
  flex-wrap: wrap;
  column-gap: 39px;
  
}

.calculator__buttons-wrapper {
  margin-top: 40px;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.calculator__prev-button,
.calculator__next-button {
  width: 240px;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  color: var(--white);
  outline: none;
  border-radius: 12px;
}

.calculator__next-button {
  background-color: var(--brand-color);
  border: 0.3px solid rgba(255, 255, 255, 0.3);
}

.calculator__prev-button {
  background-color: inherit;
  border: 1px solid var(--Brand-color, #1c6eda);
}

.pagination__element,
.pagination__element-first {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: rgba(217, 217, 217, 0.1);
  transition: 0.25s ease-in-out;
}


.calculator__pagination-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 30px;
}

.pagination__element.active,
.pagination__element-first {
  background-color: var(--brand-color);
}

.calculator__button-first {
  width: 260px;
  height: 80px;
  border-radius: 12px;
  border: 0.3px solid rgba(255, 255, 255, 0.3);
  background: #111;
  color: var(--white);
}

.calculator__questions-other {
  margin-top: 31px;
  height: 210px;
  display: flex;
  row-gap: 30px;
  flex-wrap: wrap;
  column-gap: 79px;
  align-content: flex-start;
}

.calculator__button-other {
  width: 240px;
  height: 60px;
  border-radius: var(--spacing-8, 8px);
  border: 0.3px solid rgba(255, 255, 255, 0.3);
  background: #111;
  color: var(--white);
  transition: .18s ease-in-out;
}

.calculator__button-other.selected {
  border: 1px solid var(--brand-color);
}

.calculator__questions-other,
.calculator__questions-wrapper {
  overflow: hidden;
  position: relative;
}

.calculator__questions-other.animate-left,
.calculator__questions-wrapper.animate-right {
  animation-duration: 0.5s;
  animation-fill-mode: both;
}

