.form__rejected-container {
    background: inherit;
    position: fixed;
    width: 503px;
    bottom: 60px;
    overflow: hidden;
    transition: 1s ease-in-out;
    right: -503px;
    /* right: 60px; */
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    z-index: 5;
  }
  
  .form__rejected-container.active {
    position: fixed;
    width: 503px;
    bottom: 60px;
    right: 60px;
    background: inherit;
    overflow: hidden;
  }
  
  .form__close-icon {
    width: 29px;
    height: 29px;
    transform: rotate(45deg);
    cursor: pointer;
    margin-right: 25px;
  }
  
  .form__rejected {
    background-color: #000;
    border-radius: 40px;
    margin-top: 15px;
    overflow: hidden;
    position: relative;
    display: flex;
    padding: 0px 0px 0px 30px;
  }
  
  .responce__elipse-rejected {
    position: absolute;
    left: -110px;
    top: -95px;
    width: 215px;
    height: 250px;
    background-color: rgba(255, 0, 0, 0.85);
    filter: blur(200px);
    border-radius: 55%;
  }
  
  .response__texts {
    margin-top: 50px;
  }
  
  .response__title {
    text-align: left;
  }
  
  .response__description {
    width: 260px;
    margin-top: 10px;
    color: var(--white-80);
    text-align: left;
  }
  
  .response__image {
    width: 357px;
    height: 243px;
  }
  