.main__form {
  margin-right: 80px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.main__form-input {
  margin-top: 30px;
  color: white;
  width: 500px;
  outline: none;
  border: none;
  border-bottom: 2px solid rgba(255, 255, 255, 0.2);
  background-color: inherit;
  text-align: left;
}

.main__form-input.error {
  margin-top: 30px;
  color: #f00;
  width: 500px;
  outline: none;
  border: none;
  border-bottom: 2px solid #f00;
  background-color: inherit;
  text-align: left;
}

.main__form-input:first-child {
  margin-top: 68px;
}

.main__form-input::placeholder {
  color: rgba(255, 255, 255, 0.3);
}

.main__form-input.error::placeholder {
  color: #f00;
}

.main__form-button {
  padding: 0;
  margin: 0;
  color: var(--brand-color);
  width: 115px;
  margin-top: 35px;
  border: none;
  background: inherit;
  outline: none;
}
